import { getRecipes, setRecipes } from "../actions/actions";

const initialState = {
    recipes: getRecipes()
};

let newState, recipes, id, name, ingredients, recipeId, ingredientsArray;

export default function recipes_state(state = initialState, action) {
    switch (action.type) {
        case "ADD_RECIPE":
            ({ name, ingredients } = action.recipe);
            newState = JSON.parse(JSON.stringify(state));

            newState.recipes.push({
                id: randomId(),
                name,
                ingredients
            });

            setRecipes(newState.recipes);

            return newState;

        case "DELETE_RECIPE":
            id = action.id;
            newState = JSON.parse(JSON.stringify(state));
            recipes = newState.recipes;

            if (recipes && recipes.length > 0) {
                newState.recipes = recipes.filter(recipe => recipe.id !== id);
            }

            setRecipes(newState.recipes);

            return newState;

        case "EDIT_RECIPE":
            ({ ingredients, name, recipeId } = action),
                (newState = JSON.parse(JSON.stringify(state))),
                ingredientsArray = [];
            
            console.log('name, ingredients, recipeId', name, ingredients, recipeId);
            recipes = newState.recipes;

            name = "" + name.length > 0 ? name : "Untitled";
            ingredients = ("" + ingredients).split(",");

            ingredients.forEach(item => {
                if (item && item.trim().length > 0) {
                    ingredientsArray.push( item.trim().toLowerCase() );
                }
            });

            for (let i = 0; i < recipes.length; i++) {
                if (recipes[i].id === recipeId) {
                    recipes[i].name = name;
                    recipes[i].ingredients = ingredientsArray;
                }
            }

            newState.recipes = recipes;
            console.log('newState', newState);
            setRecipes(newState.recipes);

            return newState;

        case "GET_RECIPES_SUCCESS":
            return Object.assign({}, state, { recipes: action.recipes });

        default:
            return state;
    }
}

function randomId() {
    return Math.random()
        .toString(36)
        .substr(2, 9);
}



