import React, { Component } from "react";
import Chip from "material-ui/Chip";
import Button from "material-ui/Button";
import Typography from "material-ui/Typography";
import Grid from "material-ui/Grid";
import ExpansionPanel, {
    ExpansionPanelSummary,
    ExpansionPanelDetails
} from "material-ui/ExpansionPanel";
import { withStyles } from "material-ui/styles";
import EditRecipe from "../../../components/modals/EditRecipe";
import { getRecipes } from "../../../actions/actions";

const styles = theme => ({
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular
    },
    chip: {
        margin: "2px"
    },
    button: {
        margin: theme.spacing.unit
    }
});

class RecipeBox extends Component {
    

    render() {
        const { recipe, classes, handleDelete } = this.props;

        return (
            <ExpansionPanel>
                <ExpansionPanelSummary
                    expandIcon={<i className="material-icons">expand_more</i>}
                >
                    <Typography className={classes.heading}>
                        {recipe.name}
                    </Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Grid container spacing={8}>
                        <Grid item xs={12} sm={8}>
                            {recipe.ingredients.map((title, i) => {
                                return (
                                    <Chip
                                        label={title}
                                        key={i}
                                        className={classes.chip}
                                    />
                                );
                            })}
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <EditRecipe recipe={recipe}/>
                            <Button
                                variant="fab"
                                color="secondary"
                                aria-label="delete"
                                className={classes.button}
                                mini
                                onClick={ev => handleDelete(recipe.id)}
                            >
                                <i className="material-icons">delete</i>
                            </Button>
                        </Grid>
                    </Grid>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        );
    }
}

export default withStyles(styles)(RecipeBox);



