import React from "react";
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { withStyles } from "material-ui/styles";
import Typography from "material-ui/Typography";
import Modal from "material-ui/Modal";
import Button from "material-ui/Button";

import Alert from "react-s-alert";
import { FormControl } from "material-ui/Form";
import Input, { InputLabel } from "material-ui/Input";
import { editRecipe } from "../../actions/actions";

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`
    };
}

const styles = theme => ({
    root: {
        display: "inline-block"
    },
    paper: {
        position: "absolute",
        width: theme.spacing.unit * 50,
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4
    },
    controls: {
        marginTop: "15px"
    },
    input: {
        margin: "10px 0"
    },
    title: {
        fontWeight: 400, 
        fontSize: "18px", 
        color: "#737171"
    }
});

class EditRecipe extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            open: false,
            recipeId: props.recipe.id,
            name: props.recipe.name,
            ingredients: props.recipe.ingredients.join(",")
        };
    }

    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    handleChangeName = event => {
        let name = event.target.value;
        this.setState({
            name: name
        });
    };

    handleChangeIngredients = event => {
        let ingredients = event.target.value;
        this.setState({
            ingredients: ingredients
        });
    };

    editRecipe = () => {
        const { dispatch } = this.props;
        let { name, ingredients, recipeId } = this.state;

        if (ingredients) {
            dispatch(editRecipe(name, ingredients, recipeId));
            this.setState({ open: false });
        } else {
            Alert.error("Ingredients required", {
                position: "bottom-right",
                effect: "slide",
                timeout: "10000"
            });
        }
    };

    render() {
        const { classes } = this.props;
        let { name, ingredients } = this.state;

        return (
            <div className={classes.root}>
                <Button
                    variant="fab"
                    aria-label="edit"
                    className={classes.button}
                    mini
                    onClick={this.handleOpen}
                >
                    <i className="material-icons">mode_edit</i>
                </Button>
                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                >
                    <div style={getModalStyle()} className={classes.paper}>
                        <Typography gutterBottom className={classes.title}>Edit recipe</Typography>
                        <FormControl fullWidth className={classes.formControl}>
                            <InputLabel>Recipe name</InputLabel>
                            <Input
                                className={classes.textField}
                                className={classes.input}
                                data-type="name"
                                fullWidth
                                value={name}
                                onChange={this.handleChangeName}
                            />
                        </FormControl>
                        <FormControl fullWidth className={classes.formControl}>
                            <InputLabel>
                                Enter ingredients, separated, by commas
                            </InputLabel>
                            <Input
                                className={classes.textField}
                                className={classes.input}
                                data-type="ingredients"
                                multiline={true}
                                fullWidth
                                defaultValue={ingredients}
                                onChange={this.handleChangeIngredients}
                            />
                        </FormControl>
                        <Button color="primary" onClick={this.editRecipe} className={classes.controls}>
                            Save
                        </Button>
                        <Button color="secondary" onClick={this.handleClose} className={classes.controls}>
                            Cancel
                        </Button>
                    </div>
                </Modal>
            </div>
        );
    }
}

EditRecipe.propTypes = {
    classes: PropTypes.object.isRequired
};

// We need an intermediary variable for handling the recursive nesting.
const EditRecipeWrapped = withStyles(styles)(EditRecipe);

export default connect()(EditRecipeWrapped);


