import React, { Component } from "react";
import { withStyles } from "material-ui/styles";
import RecipeItem from "./RecipeItem";
import { connect } from 'react-redux';
import { deleteRecipe } from '../../../actions/actions';


const styles = theme => ({
    root: {
        flexGrow: 1,
        padding: 5
    }
});

class RecipeBox extends Component {
    constructor(props) {
        super(props);

        this.handleDelete = this.handleDelete.bind(this);
    }

    handleDelete = id => {
        const { dispatch } = this.props;
        dispatch(deleteRecipe(id));
    };

    render() {
        const  { classes, recipes } = this.props;

        return (
            <div>
                {(recipes && recipes.length > 0)
                    ? recipes.map((recipe, i) => {
                          return (
                              <div className={classes.root} key={i}>
                                  <RecipeItem recipe={recipe} handleDelete={this.handleDelete}/>
                              </div>
                          );
                      })
                    : "You do not have any recipe yet."}
            </div>
        );
    }
}

// export default withStyles(styles)(RecipeBox);
const StyledRecipeBox = withStyles(styles)(RecipeBox);

function mapStateToProps(state) {
    return {
        recipes: state.recipes
    };
}

export default connect(mapStateToProps)(StyledRecipeBox);


