import React from "react";
import PropTypes from "prop-types";
import { connect } from 'react-redux';
import { withStyles } from "material-ui/styles";
import Typography from "material-ui/Typography";
import Modal from "material-ui/Modal";
import Button from "material-ui/Button";

import Alert from "react-s-alert";
import { FormControl } from "material-ui/Form";
import Input, { InputLabel } from "material-ui/Input";
import { getRecipes, addRecipe } from "../../actions/actions";

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`
    };
}

const styles = theme => ({
    paper: {
        position: "absolute",
        width: theme.spacing.unit * 50,
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4
    },
    addButton: {
        margin: "5px 5px 15px 5px"
    },
    controls: {
        marginTop: "15px"
    },
    input: {
        margin: "10px 0"
    },
    title: {
        fontWeight: 400, 
        fontSize: "18px", 
        color: "#737171"
    }
});

class AddRecipe extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            open: false,
            name: null,
            ingredients: null
        };
    }

    handleOpen = () => {
        this.setState({ open: true });
    };

    handleClose = () => {
        this.setState({ open: false });
    };

    handleChangeName = event => {
        let name = event.target.value;
        this.setState({
            name: name
        });
    };

    handleChangeIngredients = event => {
        let ingredients = event.target.value;
        this.setState({
            ingredients: ingredients
        });
    };

    addRecipe = () => {
        let { name, ingredients } = this.state,
            ingredientsArray = [],
            recipes = getRecipes();

        let { dispatch } = this.props;

        if (ingredients) {
            name = "" + name.length > 1 ? name : "Untitled";
            ingredients = ("" + ingredients).split(",");

            ingredients.forEach(item => {
                const ingridient = item.trim().toLowerCase();
                if (ingridient) {
                    ingredientsArray.push(ingridient);
                }
            });

            const recipe = {
                name: name,
                ingredients: ingredientsArray
            };

            recipes.push(recipe);

            dispatch(addRecipe(recipe));

            this.setState({ open: false });
        } else {
            Alert.error("Ingredients required", {
                position: "bottom-right",
                effect: "slide",
                timeout: 10000
            });
        }
    };

    render() {
        const { classes } = this.props;

        return (
            <div>
                <Button
                    variant="raised"
                    color="primary"
                    onClick={this.handleOpen}
                    className={classes.addButton}
                >
                    Add recipe
                </Button>
                <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                >
                    <div style={getModalStyle()} className={classes.paper}>
                        <Typography gutterBottom className={classes.title}>
                            Add new recipe to your list!
                        </Typography>
                        <FormControl fullWidth className={classes.formControl}>
                            <InputLabel>Recipe name</InputLabel>
                            <Input
                                className={classes.textField}
                                className={classes.input}
                                data-type="name"
                                fullWidth
                                onChange={this.handleChangeName}
                            />
                        </FormControl>
                        <FormControl fullWidth className={classes.formControl}>
                            <InputLabel>
                                Enter ingredients, separated, by commas
                            </InputLabel>
                            <Input
                                className={classes.textField}
                                className={classes.input}
                                data-type="ingredients"
                                multiline={true}
                                fullWidth
                                onChange={this.handleChangeIngredients}
                            />
                        </FormControl>
                        <Button color="primary" onClick={this.addRecipe} className={classes.controls}>
                            Save
                        </Button>
                        <Button color="secondary" onClick={this.handleClose} className={classes.controls}>
                            Cancel
                        </Button>
                    </div>
                </Modal>
            </div>
        );
    }
}

AddRecipe.propTypes = {
    classes: PropTypes.object.isRequired
};

const AddRecipeWrapped = withStyles(styles)(AddRecipe);

export default connect()(AddRecipeWrapped);



